<template>
	<div class="duihuan">
		<ul>
			<li  v-for="item in items">
				<img :src="item.src"/>
				<div>
					<div>{{item.name}}</div>
					<div class="progressbar"></div>
					<div class="sales flex-item">
						<p class="default-color">已抢购{{item.saled}}件</p>
						<span class="active-color">剩余{{item.stock}}件</span>
					</div>
					<div class="bottominfors flex-item">
						<div>
							<p class="active-color price">￥{{item.price1}}</p>
							<del>￥{{item.price2}}</del>
						</div>
						<button class="active-btn">马上抢</button>
					</div>
				</div>
			</li>
			
		</ul>
		
	</div>
</template>

<script>
export default {
	data() {
		return {
			items: [
				{
				 src:'../../../static/03.jpg',
					name: '限时秒杀限时秒杀限时秒杀限时秒杀限时秒杀限时1',
					saled: '100',
					stock:'2',
					price1:0.00,
					price2:1000.00
				},
				{
				 src:'../../../static/03.jpg',
					name: '限时秒杀限时秒杀限时秒杀限时秒杀限时秒杀限时2',
					saled: '100',
					stock:'2',
					price1:0.00,
					price2:2000.00
				}
			]
		}
	},
	
	methods: {
		getOtherShop(page){
    	this.post('home/Category/getOtherShop',{page:page,number:20}).then(function(res){
    		console.log(res)
//  		if(res.data.code===10000){
//  			this.items=res.data.info.data;    		
//  			
//  			this.items.forEach(function(item){
//  				item.pic_url=this.imageURL(item.pic_url)
//  				
//  			}.bind(this))
//  		}
    	}.bind(this))
    	}
	},
	mounted() {
		this.getOtherShop(1)
	}
}
</script>

<style>
/**/
.active-color{color: #FF5773;}
.default-color{color: #999;}
.progressbar{width: 100%;height: .15rem;background: #FEB63E;border-radius: 1rem;margin-top: .15rem;}
.sales{margin-bottom: .2rem;}
.flex-item{display: flex;flex-direction: row;justify-content: space-between;align-items: center;}
.active-btn{
	border: 0;outline: none;background: #FF5773;color: #fff;
padding: .16rem .2rem;border-radius: 1rem;width:1.7rem;
}
.default-btn{
	border: 0;outline: none;background: #DDDDDD;color: #fff;
padding: .16rem .2rem;border-radius: 1rem;width:1.7rem;
}
.bottominfors{margin-top: .15rem;}
.bottominfors .price{font-size: .4rem;}
/**/
.duihuan{background: #fff;padding-left: .2rem;}
.duihuan li{display: flex;padding: .2rem;padding-left: 0;border-bottom: 1px #e6e6e6 solid;flex-direction: row;align-items: center;}
.duihuan li:last-child{border-bottom: 0;}
.duihuan img{width: 2.4rem;height: 2.4rem;margin-right: .2rem;}
/*.duihuan p{color: #FF5773;font-size: .3rem;margin: .2rem 0 .25rem;}*/
.duihuan del{color: #999;font-size: .22rem;}
/*.duihuan button{border: 0;outline: none;background: #fff;
padding: .16rem .2rem;border-radius: 4px;width:1.7rem;
}*/
.duihuan .state1{color: #FF5773;border: 1px #FF5773 solid;}
.duihuan .state2{color: #999;border: 1px #999 solid;}
</style>


